.tab {
  overflow: hidden;
  border: 1px solid #ccc;
  background-color: #f1f1f1;
}

/* Style the buttons inside the tab */
.tab button {
  background-color: inherit;
  float: left;
  border: none;
  outline: none;
  cursor: pointer;
  padding: 14px 16px;
  transition: 0.3s;
  font-size: 17px;
}

/* Change background color of buttons on hover */
.tab button:hover {
  background-color: #ddd;
}

/* Create an active/current tablink class */
.tab button.active {
  background-color: #ccc;
}

/* Style the tab content */
.tabcontent {
  display: none;
  padding: 6px 12px;
  border: 1px solid #ccc;
  border-top: none;
}

#Favorites {
  display: block;
}

.profile-details {
  display: flex;
  flex-direction: column
}

.profile-preferences {
  align-self: center;
  margin: 0 auto;  
  text-align: center;
}

@media only screen and (max-width: 400px) {
    .profile-preferences {
      width: 100%;      
    }
}

.profile-name {
  font-size: 3em;
}

@media only screen and (max-width: 400px) {
  .profile-name {
    text-align: center;
  }
}

.profile-header {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column-reverse;
  align-items: center;
}

/* --------------CSS for results-------------- */
#results {
  max-width: 300px;
  width: 100%;
  display: none;
}

.result-display {
  display: flex;
  background-color: #fff;
  transition: 0.2s;
}

.result-display:hover {
  background-color: rgba(192, 192, 192, 1);
}

.result-image {
  height: 100px;
  width: 100px;
}

.result-name {
  color: black;
  font-size: 0.8em;
}

.result-name a {
  text-decoration: none;
}
